import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProfileComponent } from './profile.component';
import { Routes, RouterModule } from '@angular/router';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { EditProfileComponent } from './edit-profile/edit-profile.component';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { AuthService } from 'src/app/service/auth.service';

const routes: Routes = [
  {
    path: '',
    component: ProfileComponent,
    data: { title: 'view_profile' }
  },
  {
    path: 'change-password',
    component: ChangePasswordComponent,
    data: { title: 'change_password' }
  },
  {
    path: 'edit',
    component: EditProfileComponent,
    data: { title: 'edit_profile' }
  }
]


@NgModule({
  declarations: [ProfileComponent, ChangePasswordComponent, EditProfileComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    FormValidationModule,
    SharedModule,
    RouterModule.forChild(routes),
    BtnAddModule
  ], providers: [
    AuthService
  ]
})
export class ProfileModule { }
